'use client';

import { useEffect, useState } from 'react';
import { useAppDispatch, useAppSelector } from '@/store/hooks';
import { 
  fetchResumoGeral,
  fetchGraficoReceitasDespesas,
  fetchGraficoDespesasPorCategoria,
  fetchGraficoReceitasPorCategoria,
  fetchEvolucaoPatrimonial
} from '@/store/slices/dashboardSlice';
import AuthGuard from '@/components/auth/AuthGuard';
import AppLayout from '@/components/layout/AppLayout';
import { 
  BarChart3, 
  TrendingUp, 
  TrendingDown, 
  Calendar,
  Wallet,
  CreditCard
} from 'lucide-react';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  BarElement,
  Title,
  Tooltip,
  Legend,
  ArcElement,
} from 'chart.js';
import { Bar, Line, Doughnut } from 'react-chartjs-2';

import {
  Card,
  CardContent,
  CardHeader,
  CardTitle,
} from "@/components/ui/card"
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select"
import { Skeleton } from "@/components/ui/skeleton"
import { cn } from "@/lib/utils"

// Registrar componentes do Chart.js
ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  BarElement,
  Title,
  Tooltip,
  Legend,
  ArcElement
);

const DashboardSkeleton = () => (
  <div className="space-y-6">
    <div className="flex justify-between items-center">
      <Skeleton className="h-10 w-48" />
      <div className="flex gap-2">
        <Skeleton className="h-10 w-32" />
        <Skeleton className="h-10 w-24" />
      </div>
    </div>
    <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
      {[...Array(4)].map((_, i) => (
        <Card key={i}>
          <CardHeader className="flex flex-row items-center justify-between pb-2">
            <Skeleton className="h-4 w-24" />
            <Skeleton className="h-4 w-4 rounded-full" />
          </CardHeader>
          <CardContent>
            <Skeleton className="h-8 w-32" />
          </CardContent>
        </Card>
      ))}
    </div>
    <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
      <Card>
        <CardHeader>
          <Skeleton className="h-5 w-48" />
        </CardHeader>
        <CardContent>
          <Skeleton className="h-[300px] w-full" />
        </CardContent>
      </Card>
      <Card>
        <CardHeader>
          <Skeleton className="h-5 w-48" />
        </CardHeader>
        <CardContent>
          <Skeleton className="h-[300px] w-full" />
        </CardContent>
      </Card>
    </div>
  </div>
);

export default function DashboardPage() {
  const dispatch = useAppDispatch();
  const { 
    resumoGeral, 
    graficoReceitasDespesas, 
    graficoDespesasPorCategoria, 
    graficoReceitasPorCategoria,
    evolucaoPatrimonial,
    isLoading
  } = useAppSelector((state) => state.dashboard);

  const [filtroMes, setFiltroMes] = useState(String(new Date().getMonth() + 1));
  const [filtroAno, setFiltroAno] = useState(String(new Date().getFullYear()));

  useEffect(() => {
    const params = { mes: Number(filtroMes), ano: Number(filtroAno) };
    dispatch(fetchResumoGeral(params));
    dispatch(fetchGraficoReceitasDespesas(params));
    dispatch(fetchGraficoDespesasPorCategoria(params));
    dispatch(fetchGraficoReceitasPorCategoria(params));
    dispatch(fetchEvolucaoPatrimonial({ meses: 6 }));
  }, [dispatch, filtroMes, filtroAno]);

  const getChartColor = (variable: string, opacity: number = 1) => {
    if (typeof window === 'undefined') return `rgba(0, 0, 0, ${opacity})`;
    
    const root = document.documentElement;
    const color = getComputedStyle(root).getPropertyValue(variable).trim();
    
    // O formato no globals.css é "221.2 83.2% 53.3%"
    // Transformamos para "hsla(221.2, 83.2%, 53.3%, opacity)"
    const formattedColor = color.split(' ').join(', ');
    return `hsla(${formattedColor}, ${opacity})`;
  };

  const formatCurrency = (value: number) => {
    return new Intl.NumberFormat('pt-BR', {
      style: 'currency',
      currency: 'BRL'
    }).format(value);
  };

  // Configurações comuns de Tooltip para manter consistência
  const commonTooltipOptions = {
    backgroundColor: 'hsl(var(--card))',
    titleColor: 'hsl(var(--foreground))',
    bodyColor: 'hsl(var(--muted-foreground))',
    borderColor: 'hsl(var(--border))',
    borderWidth: 1,
    padding: 12,
    cornerRadius: 8,
    displayColors: true,
    usePointStyle: true,
    boxPadding: 6,
    titleFont: {
      size: 13,
      weight: 'bold' as const,
    },
    bodyFont: {
      size: 12,
    },
    callbacks: {
      label: (context: any) => {
        let label = context.dataset.label || '';
        if (label) label += ': ';
        if (context.parsed.y !== null) {
          label += formatCurrency(context.parsed.y);
        }
        return ` ${label}`;
      }
    }
  };

  const commonScalesOptions = {
    x: {
      grid: {
        display: false,
      },
      ticks: {
        font: {
          size: 11,
        },
        color: 'hsl(var(--muted-foreground))',
      }
    },
    y: {
      grid: {
        color: 'hsl(var(--border) / 0.4)',
        drawTicks: false,
      },
      border: {
        dash: [4, 4],
        display: false,
      },
      ticks: {
        font: {
          size: 10,
        },
        color: 'hsl(var(--muted-foreground))',
        callback: (value: any) => formatCurrency(value as number),
        padding: 10,
      }
    }
  };

  const doughnutTooltipOptions = {
    ...commonTooltipOptions,
    callbacks: {
      label: (context: any) => {
        const total = context.dataset.data.reduce((a: any, b: any) => a + b, 0);
        const value = context.parsed;
        const percentage = ((value / total) * 100).toFixed(1);
        return ` ${context.label}: ${formatCurrency(value)} (${percentage}%)`;
      }
    }
  };

  const meses = [
    { value: "1", label: 'Janeiro' },
    { value: "2", label: 'Fevereiro' },
    { value: "3", label: 'Março' },
    { value: "4", label: 'Abril' },
    { value: "5", label: 'Maio' },
    { value: "6", label: 'Junho' },
    { value: "7", label: 'Julho' },
    { value: "8", label: 'Agosto' },
    { value: "9", label: 'Setembro' },
    { value: "10", label: 'Outubro' },
    { value: "11", label: 'Novembro' },
    { value: "12", label: 'Dezembro' },
  ];

  const anos = Array.from({ length: 10 }, (_, i) => {
    const year = new Date().getFullYear() - 5 + i;
    return String(year);
  });

  return (
    <AuthGuard>
      <AppLayout>
        {isLoading && !resumoGeral ? (
          <DashboardSkeleton />
        ) : (
          <div className="space-y-8 animate-in fade-in duration-500">
            {/* Cabeçalho e Filtros */}
            <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4 pb-2 border-b">
              <div>
                <h1 className="text-3xl font-bold tracking-tight text-foreground">Dashboard</h1>
                <p className="text-sm text-muted-foreground mt-1">
                  Visão geral da sua saúde financeira
                </p>
              </div>
              
              <div className="flex items-center gap-2 bg-muted/30 p-1 rounded-lg border border-border/50">
                <div className="w-[140px]">
                  <Select value={filtroMes} onValueChange={setFiltroMes}>
                    <SelectTrigger className="bg-transparent border-none shadow-none focus:ring-0">
                      <Calendar className="mr-2 h-4 w-4 text-muted-foreground" />
                      <SelectValue placeholder="Mês" />
                    </SelectTrigger>
                    <SelectContent>
                      {meses.map((mes) => (
                        <SelectItem key={mes.value} value={mes.value}>{mes.label}</SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                </div>
                <div className="w-[1px] h-4 bg-border/50" />
                <div className="w-[100px]">
                  <Select value={filtroAno} onValueChange={setFiltroAno}>
                    <SelectTrigger className="bg-transparent border-none shadow-none focus:ring-0">
                      <SelectValue placeholder="Ano" />
                    </SelectTrigger>
                    <SelectContent>
                      {anos.map((ano) => (
                        <SelectItem key={ano} value={ano}>{ano}</SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                </div>
              </div>
            </div>

            {/* Cards de Resumo */}
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
              {/* Receitas */}
              <Card className="group border shadow-sm transition-all hover:shadow-md hover:border-success/20 bg-card overflow-hidden">
                <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                  <CardTitle className="text-[10px] font-bold text-muted-foreground uppercase tracking-widest">
                    Receitas
                  </CardTitle>
                  <div className="p-2 bg-success/10 rounded-xl group-hover:bg-success/20 transition-colors shadow-sm">
                    <TrendingUp className="h-4 w-4 text-success" />
                  </div>
                </CardHeader>
                <CardContent>
                  <div className="text-2xl font-bold text-success tracking-tight">
                    {resumoGeral?.receitas?.total !== undefined ? formatCurrency(resumoGeral.receitas.total) : 'R$ 0,00'}
                  </div>
                  <p className="text-xs text-muted-foreground mt-1.5 flex items-center gap-1.5">
                    <span className="w-1 h-1 rounded-full bg-success/50" />
                    Total recebido no mês
                  </p>
                </CardContent>
              </Card>

              {/* Despesas */}
              <Card className="group border shadow-sm transition-all hover:shadow-md hover:border-destructive/20 bg-card overflow-hidden">
                <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                  <CardTitle className="text-[10px] font-bold text-muted-foreground uppercase tracking-widest">
                    Despesas
                  </CardTitle>
                  <div className="p-2 bg-destructive/10 rounded-xl group-hover:bg-destructive/20 transition-colors shadow-sm">
                    <TrendingDown className="h-4 w-4 text-destructive" />
                  </div>
                </CardHeader>
                <CardContent>
                  <div className="text-2xl font-bold text-destructive tracking-tight">
                    {resumoGeral?.despesas?.total !== undefined ? formatCurrency(resumoGeral.despesas.total) : 'R$ 0,00'}
                  </div>
                  <p className="text-xs text-muted-foreground mt-1.5 flex items-center gap-1.5">
                    <span className="w-1 h-1 rounded-full bg-destructive/50" />
                    Total gasto no mês
                  </p>
                </CardContent>
              </Card>

              {/* Saldo */}
              <Card className="group border shadow-sm transition-all hover:shadow-md hover:border-primary/20 bg-card overflow-hidden">
                <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                  <CardTitle className="text-[10px] font-bold text-muted-foreground uppercase tracking-widest">
                    Saldo Total
                  </CardTitle>
                  <div className="p-2 bg-primary/10 rounded-xl group-hover:bg-primary/20 transition-colors shadow-sm">
                    <Wallet className="h-4 w-4 text-primary" />
                  </div>
                </CardHeader>
                <CardContent>
                  <div className={cn(
                    "text-2xl font-bold tracking-tight",
                    (resumoGeral?.saldo ?? 0) >= 0 ? 'text-primary' : 'text-destructive'
                  )}>
                    {resumoGeral?.saldo !== undefined ? formatCurrency(resumoGeral.saldo) : 'R$ 0,00'}
                  </div>
                  <p className="text-xs text-muted-foreground mt-1.5 flex items-center gap-1.5">
                    <span className={cn(
                      "w-1 h-1 rounded-full",
                      (resumoGeral?.saldo ?? 0) >= 0 ? 'bg-primary/50' : 'bg-destructive/50'
                    )} />
                    Saldo atual em contas
                  </p>
                </CardContent>
              </Card>

              {/* Cartões */}
              <Card className="group border shadow-sm transition-all hover:shadow-md hover:border-warning/20 bg-card overflow-hidden">
                <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                  <CardTitle className="text-[10px] font-bold text-muted-foreground uppercase tracking-widest">
                    Percentual Gasto
                  </CardTitle>
                  <div className="p-2 bg-warning/10 rounded-xl group-hover:bg-warning/20 transition-colors shadow-sm">
                    <CreditCard className="h-4 w-4 text-warning" />
                  </div>
                </CardHeader>
                <CardContent>
                  <div className="text-2xl font-bold tracking-tight text-foreground">
                    {resumoGeral?.percentualDespesa !== undefined ? `${resumoGeral.percentualDespesa.toFixed(1)}%` : '0%'}
                  </div>
                  <p className="text-xs text-muted-foreground mt-1.5 flex items-center gap-1.5">
                    <span className="w-1 h-1 rounded-full bg-warning/50" />
                    Gasto sobre receita
                  </p>
                </CardContent>
              </Card>
            </div>

            {/* Gráficos Linha 1 */}
            <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
              {/* Receitas vs Despesas */}
              <Card className="border shadow-sm bg-card overflow-hidden">
                <CardHeader className="border-b bg-muted/10">
                  <CardTitle className="flex items-center text-sm font-semibold uppercase tracking-wider text-muted-foreground">
                    <BarChart3 className="h-4 w-4 mr-2 text-primary" />
                    Receitas vs Despesas
                  </CardTitle>
                </CardHeader>
                <CardContent className="pt-6">
                  <div className="h-80 flex items-center justify-center">
                    {graficoReceitasDespesas ? (
                      <Bar
                        data={{
                          labels: graficoReceitasDespesas.labels,
                          datasets: [
                            {
                              label: 'Receitas',
                              data: graficoReceitasDespesas.receitas,
                              backgroundColor: getChartColor('--success'),
                              hoverBackgroundColor: getChartColor('--success', 0.8),
                              borderRadius: 6,
                              barThickness: 12,
                            },
                            {
                              label: 'Despesas',
                              data: graficoReceitasDespesas.despesas,
                              backgroundColor: getChartColor('--destructive'),
                              hoverBackgroundColor: getChartColor('--destructive', 0.8),
                              borderRadius: 6,
                              barThickness: 12,
                            },
                          ],
                        }}
                        options={{
                          responsive: true,
                          maintainAspectRatio: false,
                          plugins: {
                            legend: {
                              position: 'bottom' as const,
                              align: 'start' as const,
                              labels: {
                                usePointStyle: true,
                                pointStyle: 'circle',
                                padding: 20,
                                font: {
                                  size: 12,
                                  weight: 'bold'
                                },
                                color: 'hsl(var(--muted-foreground))'
                              }
                            },
                            tooltip: commonTooltipOptions
                          },
                          scales: commonScalesOptions
                        }}
                      />
                    ) : (
                      <p className="text-muted-foreground text-sm italic">Carregando dados...</p>
                    )}
                  </div>
                </CardContent>
              </Card>

              {/* Evolução Patrimonial */}
              <Card className="border shadow-sm bg-card overflow-hidden">
                <CardHeader className="border-b bg-muted/10">
                  <CardTitle className="flex items-center text-sm font-semibold uppercase tracking-wider text-muted-foreground">
                    <TrendingUp className="h-4 w-4 mr-2 text-primary" />
                    Evolução Patrimonial
                  </CardTitle>
                </CardHeader>
                <CardContent className="pt-6">
                  <div className="h-80 flex items-center justify-center">
                    {evolucaoPatrimonial ? (
                      <Line
                        data={{
                          labels: evolucaoPatrimonial.labels,
                          datasets: [
                            {
                              label: 'Patrimônio',
                              data: evolucaoPatrimonial.valores,
                              borderColor: 'hsl(var(--primary))',
                              backgroundColor: (context: any) => {
                                const ctx = context.chart.ctx;
                                const gradient = ctx.createLinearGradient(0, 0, 0, 400);
                                gradient.addColorStop(0, getChartColor('--primary', 0.2));
                                gradient.addColorStop(1, getChartColor('--primary', 0));
                                return gradient;
                              },
                              tension: 0.4,
                              fill: true,
                              pointRadius: 0,
                              pointHoverRadius: 6,
                              pointHoverBackgroundColor: 'hsl(var(--primary))',
                              pointHoverBorderColor: 'hsl(var(--background))',
                              pointHoverBorderWidth: 3,
                              borderWidth: 3,
                            },
                          ],
                        }}
                        options={{
                          responsive: true,
                          maintainAspectRatio: false,
                          interaction: {
                            intersect: false,
                            mode: 'index',
                          },
                          plugins: {
                            legend: { display: false },
                            tooltip: commonTooltipOptions
                          },
                          scales: commonScalesOptions
                        }}
                      />
                    ) : (
                      <p className="text-muted-foreground text-sm italic">Carregando dados...</p>
                    )}
                  </div>
                </CardContent>
              </Card>
            </div>

            {/* Gráficos Linha 2 - Categorias */}
            <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
              {/* Despesas por Categoria */}
              <Card className="border shadow-sm bg-card overflow-hidden">
                <CardHeader className="border-b bg-muted/10">
                  <CardTitle className="flex items-center text-sm font-semibold uppercase tracking-wider text-muted-foreground">
                    <TrendingDown className="h-4 w-4 mr-2 text-destructive" />
                    Despesas por Categoria
                  </CardTitle>
                </CardHeader>
                <CardContent className="pt-6">
                  <div className="h-80 flex items-center justify-center">
                    {graficoDespesasPorCategoria && graficoDespesasPorCategoria.labels.length > 0 ? (
                      <Doughnut
                        data={{
                          labels: graficoDespesasPorCategoria.labels,
                          datasets: [
                            {
                              data: graficoDespesasPorCategoria.valores,
                              backgroundColor: graficoDespesasPorCategoria.cores.map(c => `${c}CC`),
                              borderColor: graficoDespesasPorCategoria.cores,
                              borderWidth: 2,
                              hoverOffset: 15,
                            },
                          ],
                        }}
                        options={{
                          responsive: true,
                          maintainAspectRatio: false,
                          cutout: '75%',
                          plugins: {
                            legend: {
                              position: 'right' as const,
                              labels: {
                                usePointStyle: true,
                                padding: 20,
                                font: { size: 11, weight: 'bold' as const },
                                color: 'hsl(var(--muted-foreground))'
                              }
                            },
                            tooltip: doughnutTooltipOptions
                          }
                        }}
                      />
                    ) : (
                      <div className="flex flex-col items-center justify-center text-muted-foreground italic text-sm">
                        <p>Sem dados de despesas neste mês</p>
                      </div>
                    )}
                  </div>
                </CardContent>
              </Card>

              {/* Receitas por Categoria */}
              <Card className="border shadow-sm bg-card overflow-hidden">
                <CardHeader className="border-b bg-muted/10">
                  <CardTitle className="flex items-center text-sm font-semibold uppercase tracking-wider text-muted-foreground">
                    <TrendingUp className="h-4 w-4 mr-2 text-success" />
                    Receitas por Categoria
                  </CardTitle>
                </CardHeader>
                <CardContent className="pt-6">
                  <div className="h-80 flex items-center justify-center">
                    {graficoReceitasPorCategoria && graficoReceitasPorCategoria.labels.length > 0 ? (
                      <Doughnut
                        data={{
                          labels: graficoReceitasPorCategoria.labels,
                          datasets: [
                            {
                              data: graficoReceitasPorCategoria.valores,
                              backgroundColor: graficoReceitasPorCategoria.cores.map(c => `${c}CC`),
                              borderColor: graficoReceitasPorCategoria.cores,
                              borderWidth: 2,
                              hoverOffset: 15,
                            },
                          ],
                        }}
                        options={{
                          responsive: true,
                          maintainAspectRatio: false,
                          cutout: '75%',
                          plugins: {
                            legend: {
                              position: 'right' as const,
                              labels: {
                                usePointStyle: true,
                                padding: 20,
                                font: { size: 11, weight: 'bold' as const },
                                color: 'hsl(var(--muted-foreground))'
                              }
                            },
                            tooltip: doughnutTooltipOptions
                          }
                        }}
                      />
                    ) : (
                      <div className="flex flex-col items-center justify-center text-muted-foreground italic text-sm">
                        <p>Sem dados de receitas neste mês</p>
                      </div>
                    )}
                  </div>
                </CardContent>
              </Card>
            </div>
          </div>
        )}
      </AppLayout>
    </AuthGuard>
  );
}
